.todo {
  display: flex;
  align-items: center;
  gap: 5px;
  margin: 1rem 0;
  padding: 15px 20px;
  font-size: 1.6rem;
  border-radius: 5px;
  border: 3px solid transparent;
  background-color: #eff7fa;
  color: black;
  user-select: none;
  animation-name: todo;
  transition: all 0.3s;
  animation-duration: 0.3s;
}

.todo:last-child {
  margin-bottom: 1.26rem;
}

.todoText {
  width: 100%;
  overflow: hidden;
  font-weight: bold;
  touch-action: auto;
  transition: all 0.3s;
}

.completedTodo {
  background-color: unset;
  color: gray;
  border: 3px solid #555;
  text-decoration: line-through;
  animation-name: completedTodo;
  transition: all 0.3s;
  animation-duration: 0.3s;
}

.removeLineThrough {
  text-decoration: none;
}

.copiedTodo {
  font-size: 1.6rem;
  font-style: italic;
  color: rgb(136, 191, 236, 0.9);
}

.todo.completedTodo .todoIcon,
.todo.completedTodo .lockIcon,
.todo.completedTodo .deleteIcon,
.todo.completedTodo .checkIcon {
  color: gray;
}

.copiedIcon,
.todoIcon,
.lockIcon,
.deleteIcon,
.checkIcon {
  cursor: pointer;
  padding: 0 5px;
  font-size: 2rem;
  border-radius: 5px;
  transition: all 0.3s;

}

.copiedIcon:hover,
.todoIcon:hover,
.lockIcon:hover,
.deleteIcon:hover,
.checkIcon:hover {
  transform: scale(1.3);
}

.copiedIcon {
  color: rgb(64, 81, 182, 0.9);
}

.copiedIcon {
  background-color: rgb(64, 81, 182, 0.1);
  color: rgb(64, 81, 182, 0.9);
}

.todoIcon {
  color: rgb(136, 191, 236, 0.9);
}

.todoIcon:hover {
  background-color: rgb(136, 191, 236, 0.1);
  color: rgb(145, 207, 255, 0.9)
}

.lockIcon {
  color: rgb(64, 81, 182, 0.2);
}

.lockIcon:hover {
  background-color: rgb(64, 81, 182, 0.1);
  color: rgb(64, 81, 182, 0.9);
}

.deleteIcon {
  color: rgb(210, 29, 38, 0.2);
}

.deleteIcon:hover {
  background-color: rgb(210, 29, 38, 0.1);
  color: rgb(210, 29, 38, 0.9)
}

.checkIcon {
  color: rgb(112, 161, 39, 0.2);
}

.checkIcon:hover {
  background-color: rgb(112, 161, 39, 0.1);
  color: rgb(149, 211, 48, 0.9)
}

.todoIcon:active,
.lockIcon:active,
.deleteIcon:active,
.checkIcon:active {
  transform: scale(0.9);
  transition: all 0.3s;
  background-color: rgb(128, 128, 128, 0.1);
}

.grabCursor {
  cursor: grab;
}

.grabCursor:active {
  opacity: 0.7;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

@media screen and (max-width: 480px) {
  .todo {
    padding: 8px 10px;
  }

  .copiedTodo,
  .todoText {
    font-size: 1.25rem;
  }
}